import React from 'react';
import '../index.css'
import hamburguesa from '../images/hamburguesa.jpg'
import {Container, Box, Grid, Paper,Typography, Button} from '@material-ui/core/'
import Footer from '../component/Footer'

const About = () => {
    const bga ={
        // zIndex:'-10',
        backgroundColor:'#EFEFEF',
        width:'100%',
        height:'100vh',
    };
    const btn ={
        color:'#fff',
        backgroundColor:'#BC1823',
        padding:'5px 35px',
        marginTop:'25px',
        borderRadius:'25px',
        fontSize:'25px',
    };
   

 
    return (
        <div style={bga}>
            <Grid className="back"  container   >
                <Grid align="center"  item lg={7} md={8} xs={12} >
                    <img width="100%"  src={hamburguesa} alt="" />
                </Grid>
                <Grid align="center"  item lg={5} md={4} xs={12}>
                <Box className="centrado" >
                <Typography  variant="h1">
                            <strong> The </strong> <br />                       
                            <strong> sunday bite</strong>
                        </Typography>
                        <Typography variant="h4">
                            Delicious food for every mood    
                </Typography>   
                </Box>
                        <Button style={btn} className="boton"  variant="outlined"  size="large" >Order Now</Button>
                </Grid>
             </Grid>
             <Footer />
        </div>
    );
}

export default About;
