import React from 'react';
import '../index.css'
import {Container, Box, Grid, Paper,Typography, Button} from '@material-ui/core/'
import menu from '../images/menu.png'
import Footer from '../component/Footer'
// import zIndex from '@material-ui/core/styles/zIndex';
// import { , Typography } from '@material-ui/core';

const Home = () => {
    const bga ={
        // zIndex:'-10',
        backgroundColor:'#EFEFEF'
    };
    const bgb = {
        backgroundColor:'#BC1823',
        width:'100%',
        height:'100vh',
    };
    const btn ={
    color:'#fff',
    backgroundColor:'#BC1823',
    padding:'5px 35px',
    marginTop:'25px',
    borderRadius:'25px',
    fontSize:'25px',

    }
  
    return (
        <div className="box">
            <Grid container >
                <Grid style={bga} item lg={8} md={8} xs={9} >
                    <div className="title">
                        <Typography  align="" variant="h1">
                            <strong> The </strong> <br />                       
                            <strong> sunday bite</strong>
                        </Typography>
                        <Typography variant="h4">
                            Delicious food for every mood    
                        </Typography>   
                        <Button style={btn} className="boton"  variant="outlined"  size="large" >Order Now</Button>
                    </div>
                </Grid>
                <Grid style={bgb} item lg={4} md={4} xs={3}>
                </Grid>
                    <img className="image-food" src={menu} alt="Logo" />
             </Grid>
             {/* <Button className=""  variant="outlined" color="secondary" size="large" >Order Now</Button>  */}
            <Footer />
        </div>
    );
}

export default Home;
