import React from 'react';
import '../index.css'
// import {Grind, Typography, Button, Grid}
import Tarjetas from '../component/Tarjetas'
import { Typography, Button, Grid } from '@material-ui/core/';
import Footer from '../component/Footer'



const Menu = () => {
    const styles ={
        paddingTop:'5%'
    }
    const styless ={
        paddingBottom:'5%'
    }
    
    return (
        
        <div>
        
              <Grid  container   >
                <Grid className="fondoMenu"  align="center"  item lg={12}  >
                    <Typography style={styles} pb="5" align="center" variant="h1">              
                        <strong> What We Offer</strong>
                    </Typography>
                    <Typography style={styless}  align="center" variant="h4">              
                         Curious? Here are our most popular menu items.
                    </Typography>    
                    <Tarjetas />
                </Grid>
             </Grid>
                <Footer />    

             
        </div>
    );
}

export default Menu;
