
import React from 'react';
import '../index.css'
import Footer from '../component/Footer'
import {Grid,Typography, Box , Value} from '@material-ui/core/'
import Rating from '@material-ui/lab/Rating';
import Flippy, { FrontSide, BackSide } from 'react-flippy';
import people from '../images/people.png'
import principal from '../images/principal.jpg'


const Client = () => {
    const styles ={
        paddingTop:'6%',
        paddingBottom:'6%',
        color:'#ffffff'
    }
    const pb ={
        paddingBottom:'40px',
        paddingTop:'40px'

    }
    const img ={
        width:'100%'
    }
    return (
        <div className="fondoCliente">
            <div className="mt-4">
                <Typography style={styles} color="inherit" variant="h1" align="center">
                    What Our Clients Say
                </Typography>
            <Grid container style={pb} align="center">
                <Grid item xs={12} sm={6} md={4} >
                        <Flippy
                            flipOnHover={true} 
                            flipDirection="horizontal" 
                            style={{ width: '300px', height: '500px' }} 
                            >
                            <FrontSide
                            style={{
                                // backgroundColor: '#41669d',
                                backgroundColor: '#A9C9FF',
                                backgroundImage: 'linear-gradient(180deg, #A9C9FF 0%, #FFBBEC 100%)'
                            }}
                            >
                            <Typography mb={5}>
                                Bife de chorizo
                            </Typography>
                                <br/>
                                <img style={img}  src={principal}/>
                            <Typography  >
                                <Box  p={2}>
                                    El tradicional restaurante con sus sucursales centricas y su buena comida. 
                                Esas papas souffle son riquisimas. El personal de mucha experiencia.
                                </Box>
                            </Typography>
                            </FrontSide>
                            <BackSide
                            style={{     
                                backgroundColor: '#FF3CAC',
                                backgroundImage: 'linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%)'
                            }}>
                            <Typography mb={4}>
                                Martin Ortiz
                            </Typography>    
                                <img style={img}  src={people}/>
                            <Typography component="" aling="center">Recomendación</Typography>
                                Muchas gracias por la atención y servicios
                            <Box component="fieldset" mb={3} borderColor="transparent">
                            <Typography component="" aling="center">Puntuación</Typography>
                            <Rating
                            // name="simple-controlled"
                            value={4}
                            precision={5}
                            // max={5}
                            size='medium'
                                />
                            </Box>
                            </BackSide>
                        </Flippy>
                </Grid>
                <Grid item xs={12}  sm={6} md={4}>
                <Flippy
                            flipOnHover={true} 
                            flipDirection="horizontal" 
                            style={{ width: '300px', height: '500px' }} 
                            >
                            <FrontSide
                            style={{
                                // backgroundColor: '#41669d',
                                backgroundColor: '#A9C9FF',
                                backgroundImage: 'linear-gradient(180deg, #A9C9FF 0%, #FFBBEC 100%)'
                            }}
                            >
                            <Typography mb={5}>
                                Bife de chorizo
                            </Typography>
                                <br/>
                                <img style={img}  src={principal}/>
                            <Typography  >
                                <Box  p={2}>
                                    El tradicional restaurante con sus sucursales centricas y su buena comida. 
                                Esas papas souffle son riquisimas. El personal de mucha experiencia.
                                </Box>
                            </Typography>
                            </FrontSide>
                            <BackSide
                            style={{     
                                backgroundColor: '#FF3CAC',
                                backgroundImage: 'linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%)'
                            }}>
                            <Typography mb={4}>
                                Martin Ortiz
                            </Typography>    
                                <img style={img}  src={people}/>
                            <Typography component="" aling="center">Recomendación</Typography>
                                Muchas gracias por la atención y servicios
                            <Box component="fieldset" mb={3} borderColor="transparent">
                            <Typography component="" aling="center">Puntuación</Typography>
                            <Rating
                            // name="simple-controlled"
                            value={4}
                            precision={5}
                            // max={5}
                            size='medium'
                                />
                            </Box>
                            </BackSide>
                        </Flippy>

                </Grid>
                <Grid item xs={12}  sm={6} md={4}>
                <Flippy
                            flipOnHover={true} 
                            flipDirection="horizontal" 
                            style={{ width: '300px', height: '500px' }} 
                            >
                            <FrontSide
                            style={{
                                // backgroundColor: '#41669d',
                                backgroundColor: '#A9C9FF',
                                backgroundImage: 'linear-gradient(180deg, #A9C9FF 0%, #FFBBEC 100%)'
                            }}
                            >
                            <Typography mb={5}>
                                Bife de chorizo
                            </Typography>
                                <br/>
                                <img style={img}  src={principal}/>
                            <Typography  >
                                <Box  p={2}>
                                    El tradicional restaurante con sus sucursales centricas y su buena comida. 
                                Esas papas souffle son riquisimas. El personal de mucha experiencia.
                                </Box>
                            </Typography>
                            </FrontSide>
                            <BackSide
                            style={{     
                                backgroundColor: '#FF3CAC',
                                backgroundImage: 'linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%)'
                            }}>
                            <Typography mb={4}>
                                Martin Ortiz
                            </Typography>    
                                <img style={img}  src={people}/>
                            <Typography component="" aling="center">Recomendación</Typography>
                                Muchas gracias por la atención y servicios
                            <Box component="fieldset" mb={3} borderColor="transparent">
                            <Typography component="" aling="center">Puntuación</Typography>
                            <Rating
                            // name="simple-controlled"
                            value={4}
                            precision={5}
                            // max={5}
                            size='medium'
                                />
                            </Box>
                            </BackSide>
                        </Flippy>
                </Grid>
            </Grid>
            <Footer />
            </div>
        </div>

    );
}

export default Client;
