import React from 'react';
import Home from '../pages/Home';
import About from '../pages/About';
import Menu from '../pages/Menu';
import Client from '../pages/Client';
import Contact from '../pages/Contact';
import {BrowserRouter as Router,Link, NavLink, Route, Switch} from 'react-router-dom'
import {AppBar, IconButton,ButtonGroup,Toolbar, Button} from '@material-ui/core/'



const Navegacion = () => {
    const Styles = {
        textDecoration:'none',
        color:'#ffffff'
    }

    const bg ={
        backgroundColor: '#293039'
    }
    return (
        <div>
            <Router>
                <AppBar style={bg} position='fixed'>
                        <Toolbar>
                            <ButtonGroup variant="underline"  aria-label="text primary button group">
                                <Button  >
                                    <Link style={Styles} to="/" > Home</Link>
                                </Button>
                                <Button >
                                    <Link style={Styles} to="/about"> About</Link>
                                </Button>
                                <Button >
                                    <Link style={Styles} to="/menu"> Menu</Link>
                                </Button>
                                <Button >
                                    <Link style={Styles} to="/client"> Client</Link>
                                </Button>
                                <Button >
                                    <Link style={Styles} to="/contact"> Contact</Link>
                                </Button>
                            </ButtonGroup>
                        </Toolbar>
                </AppBar>
                
                <Switch>
                    <Route path="/" exact>
                        <Home />
                    </Route>
                    <Route path="/about">
                        <About />
                    </Route>
                    <Route path="/menu">
                        <Menu />
                    </Route>
                    <Route path="/client">
                        <Client />
                    </Route>
                    <Route path="/contact">
                        <Contact />
                    </Route>
                </Switch>
            </Router>


        </div>
    );
}

export default Navegacion;
