import React from 'react';
import Tar1 from './card/Tar1';
import Tar2 from './card/Tar2';
import Tar3 from './card/Tar3';

import { Grid } from '@material-ui/core/';


const Tarjetas = () => {

    
    return (
        <div>
            <Grid 
            container 
            justify="space-evenly"

            >
                <Grid item  xl={4}>
                    <Tar1 />    
                </Grid>
                <Grid item xl={4}>
                    <Tar2 />    
                </Grid>
                <Grid item xl={4}>
                    <Tar3 />    
                </Grid>
            </Grid>
           
        </div>
    );
}

export default Tarjetas;

