import React from 'react';
import '../index.css'
import fotoContacto from '../images/paella.png'
import {Grid, Typography, Box} from '@material-ui/core'
import Footer from '../component/Footer'


const Contact = () => {
    const images ={
        marginTop:'25%',
        marginLeft:'200px'
    }
    return (
        <div>

            <div className="fondoContacto">
                <Grid container >    
                    <Grid item lg={4} md={4} xs={12} >
                        <Box align="center">
                            <img style={images} src={fotoContacto}/>
                        </Box>
                    </Grid>
                    <Grid item lg={8} md={8} xs={12} >
                                <Box className="textContacto" >
                                    <Typography align="center" alignItems="center" variant="h1">
                                        <strong> Contact Us </strong> <br />                       
                                    </Typography>
                                    <Typography align="center" variant="h4">
                                        Email Address
                                    </Typography>
                                    <Typography align="center" variant="h6">
                                        contacto@gmail.com
                                    </Typography>
                                    <Typography align="center" variant="h4">
                                        Mailing Address
                                    </Typography>
                                    <Typography align="center" variant="h6">
                                        123 Anywhere St. Any City, ST 12345
                                    </Typography>
                                    <Typography align="center" variant="h4">
                                        Phone Number
                                    </Typography>
                                    <Typography align="center" variant="h6">
                                        (123) 456-7890
                                    </Typography>  
                                </Box>
                    </Grid>
            </Grid>
                
            </div>
                <Footer />
        </div>
    );
}

export default Contact;
